import React, { Component } from 'react';
import SearchRecipe from './comp/SearchRecipe';
import Results from './comp/Results';
import Header from './comp/Header';

const appID="b5d1b80f";
const appKey="bd78d79cfba8e99c66e1d9bcd30ff2d1";

class App extends Component {
  constructor() {
    super()
    this.state = {
      recipes: []
    }
    this.search = this.search.bind(this)
  }

  async search(e) {
    e.preventDefault();
    const searchQuery = e.target.elements.searchQuery.value;
    const url = 'https://api.edamam.com/search?q='+searchQuery+'&app_id='+appID+'&app_key='+appKey;
    const apiCall = await fetch(url);
    const response = await apiCall.json();

    this.setState({
      recipes: response.hits.map(hit =>
         <Results 
          key={hit.recipe.url}
          name={hit.recipe.label}
          img={hit.recipe.image}
          desc={hit.recipe.dietLabels[0] ? hit.recipe.dietLabels[0] : "N/a"}
          ingredients={hit.recipe.ingredients}
          calories={Math.round(hit.recipe.calories)}
          link={hit.recipe.url} 
        />
        )
    })
  }

  render() {
    return (
      <div className="App">
        <Header />
        <SearchRecipe search={this.search}/>
        {this.state.recipes}
      </div>
    );
  }
}

export default App;



